<template>
<!-- 已支付支付信息 -->
    <div class="order-nopay">
        <div class="title">支付信息</div>
        
        <div class="content">
            
            <div class="item">
                <div class="item-left">
                    <span>收银员</span>
                </div>
                <div class="item-right">
                    <span>{{ payinfo.employee_name  }}</span>
                </div>
            </div>

            <div class="item">
                <div class="item-left">
                    <span>支付时间</span>
                </div>
                <div class="item-right">
                    <span>{{ payinfo.pay_time  }}</span>
                </div>
            </div>
            
            <div class="item">
                <div class="item-left">
                    <span>支付状态</span>
                </div>
                <div class="item-right">
                    <span>{{ payStatus.toString(payinfo.pay_status)  }}</span>
                </div>
            </div>

            <div class="item">
                <div class="item-left">
                    <span>支付方式</span>
                </div>
                <div class="item-right">
                    <span>{{ formPayWay.toString(payinfo.pay_way)  }}</span>
                </div>
            </div>

            <div class="item">
                <div class="item-left">
                    <span>订单金额</span>
                </div>
                <div class="item-right">
                    <span>&yen; {{ payinfo.order_fee  }}</span>
                </div>
            </div>

            <div class="item">
                <div class="item-left">
                    <span>抹零</span>
                </div>
                <div class="item-right">
                    <span>&yen; {{ payinfo.order_waiver_fee  }}</span>
                </div>
            </div>

            <div class="item">
                <div class="item-left">
                    <span>减免</span>
                </div>
                <div class="item-right">
                    <span>&yen; {{ payinfo.order_waiver_fee  }}</span>
                </div>
            </div>
            <div class="item border"></div>
            <div class="item">
                <div class="item-left">
                    <span>应收</span>
                </div>
                <div class="item-right">
                    <span>&yen; {{ payinfo.order_payable  }}</span>
                </div>
            </div>
            <div class="item">
                <div class="item-left">
                    <span>实收</span>
                </div>
                <div class="item-right">
                    <span>&yen; {{ payinfo.paid_price  }}</span>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
import { payStatus, formPayWay } from "@/config/cfg";
export default {
  props: {
    payinfo: {
      type: Object
    }
  },
  data() {
    return {
      payStatus: payStatus,
      formPayWay: formPayWay
    };
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.title {
  @include fc(14px,#666);
  margin: 20px 0;
  font-weight: bold;
}
.content {
  border: 1px solid #d8d8d8;
  padding-top: 20px;
  width: 100%;
}
.item {
  margin-bottom: 20px;

  .item-left {
    @include fc(14px,#000);
    display: inline-block;
    width: 106px;
    opacity: 0.5708;
    text-align: right;
  }
  .item-right {
    @include fc(14px,#333);
    display: inline-block;
    padding-left: 14px;
  }
}
.border {
  border-top: 1px solid #ccc;
  margin-left: 35px;
  margin-right: 35px;
}
</style>



